// main styling for whole app within body
 body {
      // profile page section
      .profile-info {
        color: orange;
        font-size: 15px;
      }

      .profile-container {
        // border: 10px solid green;
      }

      .searchBar {
        max-width: 410px;
        width: 100%;         
      }
      

      .searchIcon {
        color: orange;
        position: fixed;
        display: block;
        top: 140px;
        // zIndex: "100",
        margin-left: 180px;
        font-size: 75px;
        cursor: pointer;
      }

      .errorMessage {
        padding-left: 20px;
      }

      
    // share button styling on post card and it works on mobile as well
    .shareButtonContainer {
      box-shadow:  0px 0px 12px 0px gray;
      text-align: center;
      padding: 5px;
      max-width: 400px;
      width: 100%;
    }


    // added this to remove scrollbar on search results when on mobile phones
    .feed-responsiveOverflow {
      height: auto;
    }

    // styling for the noMatch component in case the user's role is set to user
    .noMatch {
      max-width: 320px;
      width: 100%;
      margin: 0 auto;
      height: 100%;
    }
 }